.wrapper {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.loading {
  opacity: 0.5;
}

.wrapper__active {
  background-color: #78c2d3;
}
.wrapper__reject {
  background-color: #e03756;
}

.logo {
  width: 100%;
  height: 100%;
}

.message {
  position: absolute;
  text-align: center;
  top: 30px;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  color: #414242;
  font-size: 0.85em;
  font-family: var(--code-font);
}

.output {
  color: #414242;
  height: 30vh;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  -webkit-transform: translateY(100%);
      -ms-transform: translateY(100%);
          transform: translateY(100%);
  -moz-tab-size: 4;
    -o-tab-size: 4;
       tab-size: 4;
  overflow-x: auto;
  line-height: 1.5;
  font-family: var(--code-font);
  background-color: rgba(255, 255, 255, 0.9);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  z-index: 10;
  margin: 0;
}

.output code {
  white-space: pre-wrap;
}

.show {
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
  padding: 30px 40px;
  border-top: 1px solid #f5f5f7;
  -webkit-box-shadow: 0 0 12px rgba(0, 0, 0, 0.1);
          box-shadow: 0 0 12px rgba(0, 0, 0, 0.1);
}

.close,
.copy {
  position: absolute;
  top: 10px;
  border: 0;
  background: none;
  cursor: pointer;
}

.close { right: 10px }
.copy { right: 30px }



